import { cloudHostnameMap } from '../config';
import { getSessionStorageKey } from '../init-config';
const isOtherLocal = (hostname) => {
    const ipRegex = /^\d{0,3}\.\d{0,3}\.\d{0,3}\.\d{0,3}$/;
    if (ipRegex.test(hostname)) {
        console.info('[INFO]  ip domainID  local');
        return true;
    }
    const ideRegex = /\.ide(\-proxy\-cn|\-proxy\-us|\-proxy\-boe|\-proxy\-boei18n|boe\-proxy\-boe|\-boe)?\.byted\.org$/;
    if (ideRegex.test(hostname)) {
        console.info('[INFO]  cloudIDE domainID  local');
        return true;
    }
    return false;
};
export const calcDomainID = (hostname) => {
    //  domainID
    const cloudDomainID = cloudHostnameMap[hostname] || (isOtherLocal(hostname) ? 'local' : undefined);
    if (cloudDomainID) {
        return cloudDomainID;
    }
    //  sessionStorage  domainID undefined
    try {
        return window.sessionStorage.getItem(getSessionStorageKey('domainID'));
    }
    catch (error) { }
    return undefined;
};
