export class CommonLibError extends Error {
    /**
     * @param module 
     * @param message 
     */
    constructor(module, message) {
        super(`[CommonLib](${module}): ${message}`);
    }
}
export function tryCatch(executor, onError) {
    try {
        return executor();
    }
    catch (error) {
        onError(error);
    }
}
